import React, { Component } from 'react';
import Header from '../../Layout/Header';
import nuclearAcidIcon from './images/nuclearAcid.png';
import './nuclearAcidQuery.css';
import { HttpClient } from '../../../Api/httpClient'
import wx from 'weui.js';

export default class NuclearAcidQuery extends Component {
    constructor(props) {
        super(props);
        this.state = {
            idNumber: ''
        };
    }
    componentDidMount() {
        HttpClient.post();
        wx.alert('');
    }
    render() {
        return (
            <div>
                <Header appTitle=""/>
                <div className="page__hd" data-r-healthy>
                    <img className="weui-media-box__thumb" src={nuclearAcidIcon} alt={'NuclearAcidIcon'}/>
                    <div className="page__title" data-r-healthy></div>
                    <div className="page__desc" data-r-healthy></div>
                </div>
                <div className="page__bd" style={{textAlign: 'center'}}>
                    <div className="weui-cells">
                        <div className="weui-cell weui-cell_input">
                            <div className="weui-cell__hd">
                                <div className="weui-label"></div>
                            </div>
                            <div className="weui-cell__bd">
                                <input className="weui-input" type="text" placeholder="" />
                            </div>
                        </div>
                        <div className="weui-cell weui-cell_input">
                            <div className="weui-cell__hd">
                                <div className="weui-label"></div>
                            </div>
                            <div className="weui-cell__bd">
                                <input className="weui-input" type="text" placeholder="" />
                            </div>
                        </div>
                        <div className="weui-cell weui-cell_input">
                            <button className="weui-btn weui-btn_primary weui-btn_inline"></button>
                            <button className="weui-btn weui-btn_default weui-btn_inline"></button>
                        </div>
                        <div className="weui-cell weui-cell_input">
                            <ul style={{fontSize: '10px', padding: '10px', textAlign: 'left'}}>
                                <li></li>
                                <li></li>
                                <li>1860</li>
                                <li></li>
                                <li>H5</li>
                                <li></li>
                            </ul>
                        </div>
                        <div style={{fontSize: '10px', textAlign: 'center'}}>  
                            
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}





